<template>
  <div class="apply">
    <div class="block">
      <p class="demonstration">上报时间</p>
      <el-date-picker
        v-model="value1"
        type="datetime"
        placeholder="选择日期时间"
      >
      </el-date-picker>
    </div>
    <p>性别</p>
    <el-select v-model="value" placeholder="请选择">
      <el-option
        v-for="item in options"
        :key="item.value"
        :label="item.label"
        :value="item.value"
      >
      </el-option>
    </el-select>
    <p>民族</p>
    <el-input v-model="input" placeholder="请输入内容"></el-input>
    <p>年收入(元)</p>
    <el-input-number
      v-model="num"
      controls-position="right"
      @change="handleChange"
      >元</el-input-number
    >

    <el-table :data="tableData" style="width: 100%">
      <el-table-column prop="date" width="180"></el-table-column>
      <el-table-column label="早餐" width="180">
        <template slot-scope="scope">
          <div>
            <input type="radio" :value="scope.row.name" v-model="aa" />
          </div>
        </template>
      </el-table-column>
      <el-table-column label="午餐">
        <template slot-scope="scope">
          <div>
            <input type="radio" :value="scope.row.name" v-model="bb" />
          </div>
        </template>
      </el-table-column>
      <el-table-column label="晚餐">
        <template slot-scope="scope">
          <div>
            <input type="radio" :value="scope.row.name" v-model="cc" />
          </div>
        </template>
      </el-table-column>
    </el-table>
    <p>您习惯在水中放糖吗</p>
    <el-radio v-model="radio" label="1">是</el-radio>
    <el-radio v-model="radio" label="2">否</el-radio>
    <p>导师的使用情况</p>
    <el-table :data="tableData1" style="width: 100%">
      <el-table-column label="编号">
        <template slot-scope="scope">
          <div>
            <input type="text" />
          </div>
        </template>
      </el-table-column>
      <el-table-column label="名称">
        <template slot-scope="scope">
          <div>
            <input type="text" />
          </div>
        </template>
      </el-table-column>
      <el-table-column label="直径">
        <template slot-scope="scope">
          <div>
            <el-input-number v-model="num2" controls-position="right"
              >元</el-input-number
            >
          </div>
        </template>
      </el-table-column>
      <el-table-column label="操作">
        <template slot-scope="scope">
          <div>
            <el-button type="primary" @click="tj">添加</el-button>
          </div>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
export default {
  data() {
    return {
      num2: "",
      tableData1: [
        {
          date: "1",
        },
      ],
      aa: "",
      bb: "",
      cc: "",
      radio: "1",
      tableData: [
        {
          date: "0-1天/周",
          name: "t1",
        },
        {
          date: "2-3天/周",
          name: "t2",
        },
        {
          date: "4-5天/周",
          name: "t3",
        },
      ],
      num: 1,
      input: "",
      options: [
        {
          value: "选项1",
          label: "男",
        },
        {
          value: "选项2",
          label: "女",
        },
        {
          value: "选项3",
          label: "不明",
        },
      ],
      value: "",
      pickerOptions: {
        shortcuts: [
          {
            text: "今天",
            onClick(picker) {
              picker.$emit("pick", new Date());
            },
          },
          {
            text: "昨天",
            onClick(picker) {
              const date = new Date();
              date.setTime(date.getTime() - 3600 * 1000 * 24);
              picker.$emit("pick", date);
            },
          },
          {
            text: "一周前",
            onClick(picker) {
              const date = new Date();
              date.setTime(date.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit("pick", date);
            },
          },
        ],
      },
      value1: "",
      value2: "",
      value3: "",
    };
  },
  mounted() {},
  methods: {
    handleChange(value) {
      console.log(value);
    },
    tj() {
      let ttp = this.tableData1.data + 1;
      this.tableData1.push({ data: ttp });
    },
  },
};
</script>

<style lang="scss">
.apply {
  width: 100%;
  height: 100%;
  text-align: left;
}
</style>
